<template>
	<view class="box">
		<title-line :titleList="title_line1"></title-line>
		<view class="my-car-box">
			<swiper class="my-car-li" :indicator-dots="true" :autoplay="false" circular="true" indicator-active-color='#F17E7E'
			 @change="cardChange1">
				<template v-for="(item,k) in car_list">
					<swiper-item class="swiper-item my-car-li" :key="k">
						<view class="my-car-li">
							<image lazy-load class="img" :src="item.img" mode="aspectFill"></image>
							<view class="zhuan-btn" @tap="cardClick1">
								转让
								<view class="iconfont iconjiaohuan"></view>
							</view>
						</view>
					</swiper-item>
				</template>
			</swiper>
			<view class="car-title" @tap="getQrcode">
				<view class="text" :class="{'active':card_active1==0}">银卡（{{silverCard}}）</view>
				<view class="text" :class="{'active':card_active1==1}">金卡（{{goldCard}}）</view>
				<view class="text" :class="{'active':card_active1==2}">钻石卡（{{diamondCard}}）</view>
			</view>
		</view>
		<template v-if="merchantSeqId">

			<view class="parent-title">
				<title-line :titleList="title_line2"></title-line>
				<view class="get-card" @tap="qrcodeShow">
					去收卡
					<view class="iconfont iconxiayibu"></view>
				</view>
			</view>
			<view class="my-car-box">
				<swiper class="my-car-li" :indicator-dots="true" :autoplay="false" circular="true" indicator-active-color='#F17E7E'
				 @change="cardChange2">
					<template v-for="(item,k) in car_list">
						<swiper-item class="swiper-item my-car-li" :key="k">
							<view class="my-car-li">
								<image lazy-load class="img" :src="item.img" mode="aspectFill"></image>
								<view class="zhuan-btn withdraw-btn" @tap="cardClick2">
									提现
									<view class="iconfont iconfenxiangtuiguang"></view>
								</view>
							</view>
						</swiper-item>
					</template>
				</swiper>
				<view class="car-title">
					<view class="text" :class="{'active':card_active2==0}">银卡（{{get_silverCard}}）</view>
					<view class="text" :class="{'active':card_active2==1}">金卡（{{get_goldCard}}）</view>
					<view class="text" :class="{'active':card_active2==2}">钻石卡（{{get_diamondCard}}）</view>
				</view>
			</view>
		</template>
		<uni-modal class="uni-modal-box" v-if="pop_show">
			<div class="uni-mask" @tap="pop_show=false"></div>
			<div class="uni-modal" v-if="!qrcode_show">
				<div class="uni-modal__hd"><strong class="uni-modal__title">请输入卡片数量</strong></div>
				<div class="uni-modal__bd">
					<view class="modal-content">
						<uni-number-box :value="transfer_num" @change="changeNum" />
					</view>
				</div>
				<div class="uni-modal__ft">
					<div class="uni-modal__btn uni-modal__btn_default" style="color: rgb(0, 0, 0);" @tap="pop_show=false"> 取消 </div>
					<div class="uni-modal__btn uni-modal__btn_primary" @tap="toScanCode"> 确认 </div>
				</div>
			</div>
			<template v-if="qrcode_show">
				<view class="qrcode-img">
					<image lazy-load @tap="saveImg" class="img" :src="qrcodeSrc" />
					<view class="tips">
						说明：商家仅使用该二维码进行收卡，不支持其他服务
					</view>
				</view>
				
			</template>

		</uni-modal>
		<canvas class="qrcode-canvas" canvas-id="qrcode" />
	</view>
</template>

<script>
	import titleLine from '@/components/title-line.vue'
	import uniNumberBox from './components/uni-number-box/uni-number-box.vue'
	import uQRCode from '@/common/js/uqrcode.js'
	export default {
		data() {
			return {
				title_line1: [{
					title: "我的卡片",
					id: 0
				}],
				title_line2: [{
					title: "收到的卡片",
					id: 0
				}],
				car_list: [{
						img: require('@/static/images/card-silver.png'),
					},
					{
						img: require('@/static/images/card-gold.png'),
					},
					{
						img: require('@/static/images/card-diamonds.png'),
					}
				],
				card_active1: 0,
				card_active2: 0,
				silverCard: 0, //银
				goldCard: 0, //金
				diamondCard: 0, //钻石
				get_silverCard: 0,
				get_goldCard: 0,
				get_diamondCard: 0,
				pop_show: false,
				transfer_num: 1, //要转让的数量
				card_map: ['SILVER_CARD', 'GOLD_CARD', 'DIAMOND_CARD'],
				merchantSeqId: "", //商户id
				qrcodeSrc: '',
				qrcode_show: false
			}
		},
		components: {
			titleLine,
			uniNumberBox
		},
		onLoad() {
			this.merchantSeqId = this.$common.getLocalSync('merchantSeqId')
			this.getMyCard();
			if (this.merchantSeqId && this.merchantSeqId != null) {
				// 商户才请求
				this.getScanCard()
				this.getQrcode()
			}
		},
		methods: {
			// 保存图片
			saveImg() {
				let self = this;
				uni.showActionSheet({
					title: '标题',
					itemList: ['保存图片到本地'],
					success: (e) => {
						uni.saveImageToPhotosAlbum({
							filePath: self.qrcodeSrc,
							success: function() {
								self.$box.confirm('已保存到本地相册，请打开相册查看',false);
								this.pop_show=false
							},
							fail: function(err) {
								self.saveImgFail(err)
							}
						});
					}
				})
			},
			// 保存图片失败
			saveImgFail(err) {
				if (err.errMsg === "saveImageToPhotosAlbum:fail auth deny" || err.errMsg ===
					"saveImageToPhotosAlbum:fail authorize no response" || err.errMsg ===
					"saveImageToPhotosAlbum:fail auth denied") { // 没有授权，重新授权，兼容iso和Android
					uni.showModal({
						title: '授权提示',
						content: "是否允许获取保存相册权限",
						success: (res) => {
							if (res.confirm) { // 点击确定，则调用相册授权
								uni.openSetting({
									success(settingdata) {
										if (settingdata.authSetting["scope.writePhotosAlbum"]) {
											uni.showToast({
												title: '获取权限成功，再次点击图片保存到相册'
											});
										} else {
											console.log("获取权限失败")
											uni.showToast({
												title: '请确定已打开保存权限',
												icon: "none"
											});
										}
									}
								})
							}
						}
					})
				} else if (err.errMsg === "saveImageToPhotosAlbum:fail file not found" || err.errMsg ===
					"saveImageToPhotosAlbum:fail file not exists" || err.errMsg ===
					"saveImageToPhotosAlbum:fail get file data fail"
				) { // 无图片，则提示
					uni.showToast({
						title: '暂无图片',
						icon: "none"
					});
				}
			},
			// 制作二维码
			getQrcode() {
				let self = this;
				uQRCode.make({
					canvasId: 'qrcode',
					text: this.merchantSeqId,
					size: 100,
					margin: 5,
					success: res => {
						this.qrcodeSrc = res
					},
					complete: () => {}
				})
			},
			// 我的卡片
			getMyCard() {
				this.$api.get('/o2oCustomerCard/getUserCardNum', {
					params: {
						customerSeqId: this.$common.getLocalSync('customerSeqId')
					}
				}).then(res => {
					if (res.code == 200) {
						let data = res.data;
						this.silverCard = data.silverCard; //银
						this.goldCard = data.goldCard; //金
						this.diamondCard = data.diamondCard; //钻石
					} 
				})
			},
			// 收到的卡片
			getScanCard() {
				this.$api.get('/o2oCustomerCard/getMerchantCardNum', {
					params: {
						merchantSeqId: this.merchantSeqId
					}
				}).then(res => {
					if (res.code == 200) {
						let data = res.data;
						this.get_silverCard = data.silverCard; //银
						this.get_goldCard = data.goldCard; //金
						this.get_diamondCard = data.diamondCard; //钻石
					}
				})
			},
			// 转让
			cardTransfer(id) {
				this.$api.post('/o2oCustomerCard/userCardTransfer', {
					"cardNum": this.transfer_num,
					"cardType": this.card_map[this.card_active1],
					"customerSeqId": this.$common.getLocalSync('customerSeqId'),
					"merchantSeqId": id,
				}).then(res => {
					if (res.code == 200) {
						this.$box.toast('转让成功');
						this.getMyCard();
					}
				})
			},
			// 改变卡片数量
			changeNum(value) {
				this.transfer_num = value
			},
			cardChange1(e) {
				this.card_active1 = e.detail.current;
			},
			cardChange2(e) {
				this.card_active2 = e.detail.current
			},
			// 转让
			cardClick1() {
				this.pop_show = true;
				this.qrcode_show = false;
				// 允许从相机和相册扫码
			},
			// 扫码
			toScanCode() {
				// 允许从相机和相册扫码
				if (!this.pop_show) return;
				this.pop_show = false;
				let self = this;
				// #ifndef H5
				uni.scanCode({
					success: function(res) {
						let scan_merchantSeqId = res.result;
						self.cardTransfer(scan_merchantSeqId)
					},
					fail: function(res) {
						self.$box.confirm('转让失败',false)
						console.log(res);
					}
				});
				// #endif
			},
			// 提现
			cardClick2() {},
			// 点击去收卡
			qrcodeShow() {
				this.pop_show = true;
				this.qrcode_show = true;
				this.getQrcode()
			},
			//适配不同屏幕大小的canvas
			getQRCodeSize() {
				var size = 0;
				try {
					var res = uni.getSystemInfoSync();
					var scale = res.windowWidth / 750; //不同屏幕下QRcode的适配比例；设计稿是750宽
					var width = 300 * scale;
					size = width;
				} catch (e) {
					// Do something when catch error
					console.log("获取设备信息失败" + e);
					size = 150;
				}
				return size;
			},

		}
	}
</script>

<style lang="scss">
	.uni-modal-box {
		position: fixed;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		z-index: 999;
		display: block;
		box-sizing: border-box;
	}

	.uni-mask {
		position: fixed;
		z-index: 999;
		top: 0;
		right: 0;
		left: 0;
		bottom: 0;
		background: rgba(0, 0, 0, .6);
	}

	.uni-modal-box .uni-modal {
		position: fixed;
		z-index: 999;
		width: 80%;
		max-width: 600rpx;
		top: 50%;
		left: 50%;
		-webkit-transform: translate(-50%, -50%);
		transform: translate(-50%, -50%);
		background-color: #fff;
		text-align: center;
		border-radius: 6rpx;
		overflow: hidden;
	}

	.uni-modal-box .uni-modal__hd {
		padding: 1em 1.6em .3em;
	}

	.uni-modal-box .uni-modal__bd {
		padding: 1.3em 1.6em 1.3em;
		min-height: 80rpx;
		font-size: 30rpx;
		line-height: 1.4;
		color: #999;
		max-height: 800rpx;
		overflow-y: auto;
	}

	.uni-modal-box .uni-modal__ft {
		position: relative;
		line-height: 96rpx;
		font-size: 36rpx;
		display: -webkit-box;
		display: -webkit-flex;
		display: flex;
	}

	.uni-modal-box .uni-modal__btn {
		display: block;
		flex: 1;
		text-decoration: none;
		-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
		position: relative;
	}

	.uni-modal__btn_primary {
		color: $color-primary;
	}

	.modal-content {
		display: flex;
		justify-content: center;
	}

	.qrcode-img {
		position: fixed;
		z-index: 1000;
		width: 600rpx;
		height: 440rpx;
		left: 50%;
		top: 50%;
		transform: translate(-50%,-50%);
		display: flex;
		justify-content: center;
		flex-wrap: wrap;
		.img {
			width: 400rpx;
			height: 400rpx;
		}
		.tips {
			width: 100%;
			text-align: center;
			color: white;
			font-size: 22rpx;
			margin-top: 10rpx;
		}
	}

	.qrcode-canvas {
		position: fixed;
		z-index: -10;
		left: -9999px;
	}

	.box {
		width: 660rpx;
		margin: 0 auto;

		.parent-title {
			display: flex;
			justify-content: space-between;

			.get-card {
				display: flex;
				align-items: center;
				color: $color-primary;
				font-size: 26rpx;

				.iconfont {
					font-size: 24rpx;
					margin-left: 10rpx;
				}
			}

		}

		.my-car-box {
			margin: 30rpx 0;

			.my-car-li {
				width: 660rpx;
				height: 400rpx;
				position: relative;
				box-shadow: 1px 6px 8px 0px rgba(46, 46, 46, 0.2);
				border-radius: 10rpx;
				background: rgba(241, 243, 245, 1);

				.img {
					width: 100%;
					height: 100%;
				}

				.zhuan-btn {
					width: 120rpx;
					height: 40rpx;
					background: rgba(254, 112, 88, 1);
					border-radius: 20rpx;
					font-size: 30rpx;
					color: white;
					display: flex;
					align-items: center;
					justify-content: center;
					position: absolute;
					right: 200rpx;
					bottom: 126rpx;
				}

				.withdraw-btn {
					background: $color-primary;
				}
			}
		}

		.car-title {
			display: flex;
			justify-content: space-between;
			font-size: 30rpx;
			color: #666;
			margin: 30rpx auto;
			width: 530rpx;

			.active {
				color: #343434;
				font-weight: 800;
			}
		}
	}
</style>
